body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.counter {
  justify-content: center;
  display: flex;
}

.counter__column {
  margin: 16px;
  display: flex;
  flex-direction: column;
  align-items: center;
  font-size: 20px;
}

.counter__score {
  font-size: 28px;
  font-weight: bold;
}
.answer-button {
  background-color: white;
  border: 2px solid black;
  font-size: 16px;
  margin: 4px;
  outline: none;
  padding: 12px 0px;
  width: 120px;
  cursor: pointer;
}

.answer-button--correct {
  background-color: yellowgreen;
  border: 2px solid black;
  font-size: 16px;
  margin: 4px;
  padding: 12px 0px;
  pointer-events: none;
  width: 120px;
}

.answer-button--incorrect {
  background-color: tomato;
  border: 2px solid black;
  font-size: 16px;
  margin: 4px;
  padding: 12px 0px;
  pointer-events: none;
  width: 120px;
}

.answer-button:hover {
  background-color: darkgray;
}

.next-button-container {
  display: flex;
  justify-content: center;
}
.kana-image {
  max-height: 300px;
  max-width: 300px;
}
.question {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.question__buttons {
  margin-top: 20px;
}
.mode-selector {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: 20px;
}

.mode-text {
  margin-bottom: 8px;
}

.mode-button {
  border: 2px solid black;
  background: none;
  cursor: pointer;
  font-size: 16px;
  letter-spacing: 0.3px;
  margin: 4px;
  padding: 12px 40px;
  width: 200px;
}

.mode-button:hover {
  border: 2px solid black;
  background-color: darkgrey;
}
.logo-container {
  border: 4px solid black;
  margin: 16px;
  width: 150px;
}

.logo-image {
  height: 75px;
  width: 75px;
}
.title-container {
  display: flex;
  flex-direction: column;
  align-items: center
}

.title {
  font-size: 24px;
  margin: 16px;
}
.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

